import React from 'react'
import  {TodosItem} from './TodosItem';
export const Todos = (props) => {
    return (
        <div className="container">
            <h3 className="my-3">Task Todo</h3>
            {props.todoslist.length===0?"No Task to do":
            props.todoslist.map((eachtodo)=>{
                return <TodosItem eachtodo={eachtodo} onDelete={props.onDelete}/>
            })
            }
            
        </div>
    )
}
