import React from 'react'
export const TodosItem = ({eachtodo,onDelete}) => {
    return (
        <div>
            <p>{eachtodo.title}</p>
            <p>{eachtodo.desc}</p>
            <button className="btn btn-sm btn-danger" onClick={()=>{onDelete(eachtodo)}}>DELETE</button>
        </div>
    )
}
