//import logo from './logo.svg';
import './App.css';
import  { Header } from './MyComponent/Header';
import  { Footer } from './MyComponent/Footer';
import  { Todos } from './MyComponent/Todos';
import React, { useState,useEffect } from 'react';
import  { AddTodo } from './MyComponent/AddTodo';
import  { About } from './MyComponent/About';
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link
} from "react-router-dom";
function App() {
  let initTodo;
  if (localStorage.getItem("todos") === null) {
    initTodo = [];
  }
  else {
    initTodo = JSON.parse(localStorage.getItem("todos"));
  }
  const onDelete = (eachtodo)=>{

          console.log("I am inside the todos",eachtodo);
          //Deletion by this way not work in React 
          //let index=todos.indexOf(eachtodo);
          //todos.splice(index,1);
          setCount(todos.filter((e)=>{
            return e!==eachtodo;
          }));
  }
  const addTodo=(addTitle,addDesc)=>{
    console.log("I am inside my addTodo");
    let sno;
    if(todos.length===0)
    {
      sno=0;
    }
    else
    {
      sno=todos[todos.length-1].sno+1;
    }
    const myTodo={
      sno:sno,
      title:addTitle,
      desc:addDesc,
    }
    setCount([...todos,myTodo]);
    console.log(myTodo);
  }
  const [todos, setCount] = useState(initTodo);
  useEffect(() => {
    localStorage.setItem("todos", JSON.stringify(todos));
  }, [todos])
  return(
  <>
  <Router>
  <Header title="My Task" searchBar={false}/>
  <Switch>
          <Route exact path="/" render={()=>
          {return(
          <>
          <AddTodo addTodo={addTodo}/>
          <Todos todoslist={todos} onDelete={onDelete}/>
          </>)
          }}>
          </Route>
          <Route exact path="/About">
          <About/>          
          </Route>
   </Switch>       
  <Footer/>
  </Router>
  </>
  );
}

export default App;
